.nowrap {
  white-space: nowrap; }

.deleteEl {
  .modal-dialog {
    max-width: 410px; }
  .modal-header,
  .modal-footer {
    border: 0; }
  .modal-header {
    padding: 30px 40px 0 30px; }
  .modal-footer {
    padding: 30px 40px 30px 30px; }
  .btn {
    max-width: 160px; } }

.no-overflow {
  overflow: hidden; }

.width {
  &__fix {
    width: 100%;
    min-width: 250px; }
  &__date {
    width: 100%;
    min-width: 125px; } }

a {
  &.table--sort__up,
  &.table--sort__down {
    padding: 0 !important; } }
